.suswrap{
    @extend html;
    height: 50px;
    background-color:red;
    opacity: 0.8;
    position: fixed;
    top:-50px;
    z-index: 10;
    .suspendsearch{
        @include public(50px);
        .suslogo{
            width: 100px;
            height: 50px;
            position: fixed;
            float: left;
            span{
                @include lineset(white,30px,50px)
            }
        }
        input{
            display: block;
            width: 648px;
            height: 30px;
            float: left;
            margin-left: 300px;
            margin-top: 8px;
            font-size: 14px;
            text-indent: 10px;
            color: black;
            outline: none;
        }
        button{
            width: 90px;
            height: 32px;
            border: none;
            background-color: transparent;
            float: left;
            @include lineset(white,16px,32px );
            margin-top: 8px;
            outline: none;
        }
    }
}
.centerlist{
    width: 100%;
    height: 536px;
    background-color: white;
    position: relative;
    .centerlist-top{
        @include public(36px);
        p{
            float: left;
            width: 200px;
            height: 36px;
            background-color: #ff0036;
            @include lineset(#fff,16px,36px);
            font-weight: bold;
            text-align: left;
            span{
                font-size: 18px;
                margin: 0 10px;
            }
        }
        ul{
            float: left;
            li{
                margin-left: 10px;
                float: left;
                cursor: pointer;
                @include lineset(#000,16px,36px);
                img{
                    width: 100px;
                }
                &:hover{
                    color: red;
                }
            }
        }
    }
    .centerlist-bottom{
        width: 100%;
        height: 500px;
        border-top: 2px solid red;
        .centerlist-bottom-main{
            position: relative;
            @include public(500px);
            .center-nav{
                width: 200px;
                height: 500px;
                background-color: #000;
                opacity: 0.65;
                color: white;
                li{
                    height: 31.5px;
                    line-height: 31.5px;
                    cursor: pointer;
                    span{
                        color: gray;
                        margin: 0 10px;
                    }
                }
            }
            .center-bottom-detail{
                display: none;
                .detail-left{
                    height: 480px;
                    width: 600px;
                    padding: 20px 20px 0 10px;
                    position: absolute;
                    background-color: #fff;
                    left: 200px;
                    top: 0;
                    li{
                        height: auto;
                        .li-left{
                            width: 100px;
                            float: left;
                            text-align: right;
                            b{
                                margin-right: 10px;
                            }
                        }
                        .li-right{
                            width: 465px;
                            float: left;
                            padding-bottom: 10px;
                            margin-bottom: 10px;
                            margin-left: 35px;
                            a{
                                margin-right: 10px;
                                cursor: pointer;
                                font-size: 14px;
                            }
                            border-bottom: 1px dashed gray;
                        }
                    }
                    li:last-child{
                        .li-right{
                            border: none;
                        }
                    }
                }
                .detail-right{
                    position: absolute;
                    width: 200px;
                    height: 470px;
                    padding: 15px;
                    background-color: #eee;
                    top: 0;
                    left: 830px;
                    img{
                        width: 200px;
                        margin-bottom: 10px;
                    }
                }
            }
        }
    }
    .scroll-bn{
        position: absolute;
        bottom: 50px;
        left: 0;
        right: 0;
        margin: 0 auto;
        span{
            display:block;
            float: left;
            width: 30px;
            height: 5px;
            background-color: #000;
            opacity: 0.5;
            margin-left: 5px;
            cursor: pointer;
        }
        span:first-child{
            margin-left: 0;
            background-color: #fff;
        }
    }
}
.center-ad{
        @include public(370px);
        margin-top: 40px;
        margin-bottom: 20px;
        div{
            float: left;
        }
        p{
            height: 54px;
            font-weight: bold;
            line-height: 54px;
            position: relative;
            font-size: 18px;
            border-bottom: 1px solid gray;
            background-color: white;
            span{
                font-size: 14px;
                margin-left: 10px;
            }
            b{
                position: absolute;
                right: 10px;
                color: gray;
                display: block;
                width: 20px;
                height: 20px;
                border: 1px solid gray;
                border-radius: 50%;
                text-align: center;
                line-height: 20px;
                top: 24px;
                font-weight: normal;
            }
        }
        img:hover{
            opacity: 0.8;
        }
        .ad-left{
            width: 234px;
            img{
                width: 234px;
            }
        }
        .ad-center{
            width: 485px;
            margin-left: 13px;
            img{
                width: 485px;
            }
        }
        .ad-right{
            width: 485px;
            margin-left: 13px;
            img{
                width: 485px;
            }
        }
    }
.center-rotate{
    @include public(327px);
    margin-bottom: 10px;
    ul{
        height: 327px;
        li{
            height: 108px;
            float: left;
            width: 122px;
            border-right: 1px solid #eee;
            border-bottom: 1px solid  #eee;
            background-color: white;
        }
        img{
            width: 122px;
            margin-top: 20px;
        }
        li:last-child{
            line-height: 108px;
            text-align: center;
            color: gray;
        }
    }
}
%modulelist{
    @include public(665px);
    margin-bottom: 20px;
    h1 img{
        width: 214px;
    }
    .left{
        position: relative;
        height: 618px;
        width: 235px;
        float: left;
        &:hover{
            opacity: 0.8;
        }
        img{
            width: 235px;
        }
        p{
            position: absolute;
            bottom: 100px;
            height: 64px;
            color: white;
            vertical-align: middle;
            span{
                display: block;
                width: 70px;
                height: 64px;
                background-color: #000;
                opacity: 0.9;
                float: left;
                font-size: 20px;
                text-align: center;
            }
            b{
                display: block;
                font-weight: normal;
                width: 150px;
                height: 64px;
                background-color: #000;
                opacity: 0.6;
                float: left;
                font-size: 20px;
                text-align: center;
            }
        }
        em{
            position: absolute;
            right: 10px;
            color: white;
            display: block;
            width: 30px;
            height: 30px;
            border: 2px solid white;
            border-radius: 50%;
            text-align: center;
            line-height: 30px;
            font-style: normal;
            bottom: 20px;
            font-size: 20px;
        }
    }
    .right{
        ul{
            margin-top: -20px;
            width: 992px;
            height: 618px;
            float: left;
            a{
                color: black;
            }
            li{
                box-sizing: border-box;
                width: 233px;
                margin-left: 15px;
                margin-top: 20px;
                padding: 10px 24px;
                height: 300px;
                float: left;
                background-color: #fff;
                &:hover{
                    outline: 1px solid red;
                }
                img{
                    width: 185px;
                }
                div{
                    width: 185px;
                    font-size: 16px;
                    font-family: "宋体";
                    height: 40px;
                    line-height: 20px;
                    overflow: hidden;
                    margin-top: 10px;
                }
                p{
                    width: 185px;
                    text-align: center;
                    font-size: 16px;
                    height: 20px;
                    margin-top: 10px;
                    line-height: 20px;
                    color: red;
                }
            }
        }
    }
}
%modulelist-h1{
    h1{
        height: 30px;
        margin-bottom: 10px;
        line-height: 30px;
        font-weight: normal;
        em{
            display: inline-block;
            background-color: #000;
            width: 4px;
            height: 20px;
            position: relative;
            top: 5px;
        }
        span{
            font-size: 16px;
            margin:0 10px;
        }
        b{
            font-size: 14px;
            font-weight: normal;
        }
    }
}
.tianmaochaoshi{
    @extend %modulelist;
}
.tiammaoguoji{
    @extend %modulelist;
    .left .last{
        top:150px;
    }
    .left .last-em{
        top:260px
    }
}
%tianmaobanner{
