.floor-nav{
    position: fixed;
    bottom: 50px;
    left: 100px;
    display: none;
    %floor-nav-pub{
        width: 36px;
        height: 36px;
        text-align: center;
        font-size: 13px;
        background-color: gray;
        color: #fff;
        margin-bottom: 2px;
        cursor: pointer;
        line-height: 36px;
    }
    div{
        @extend %floor-nav-pub;
        background-color: red;
    }
    ul{
        li{
            @extend %floor-nav-pub;
            line-height: 13px;
            padding-top: 5px;
            box-sizing: border-box;
        }
    }
    p{
        @extend %floor-nav-pub;
        background-color: #ccc;
    }
}